@import url(https://fonts.googleapis.com/css2?family=Fira+Sans:wght@600&display=swap);
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

a {
  text-decoration: none;
  color: lightgray;
}

body {
  font-family: 'Fira Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #282c34;
  color: white;
  max-width: 1100px;
  margin: 0 auto;
}

.App{padding:20px;text-align:left}.won-card{background-color:#4da375;border-radius:10px}.lost-card{background-color:#a34d5d;border-radius:10px}

p{color:lightgray}.MatchDetailCard{display:grid;grid-template-columns:1fr 1fr;padding:10px;margin-bottom:20px}.MatchDetailCard .match-data,.MatchDetailCard .match-venue,.MatchDetailCard .match-result{margin-top:10px}.MatchDetailCard .additional-detail{text-align:right}.MatchDetailCard .additional-detail>p{margin-bottom:20px}

.MatchSmallCard{padding:10px;margin-bottom:20px}.MatchSmallCard .match-result{margin-top:10px}

.TeamPage{display:grid;grid-template-columns:1fr 1fr 1fr 1fr;grid-template-rows:1fr 1fr 1fr;grid-column-gap:10px;grid-row-gap:10px}.TeamPage .team-name-section{grid-column:span 3;justify-self:start;align-self:end}.TeamPage .team-name-section .team-name{margin-bottom:20px;font-size:4rem}.TeamPage .win-loss-section{justify-self:center;padding:30px;text-align:center}.TeamPage .match-detail-section{grid-column:span 4}.TeamPage .more-link{-webkit-align-self:center;align-self:center;justify-self:center;font-size:1.5rem}h3{margin-bottom:10px}

.YearSelector{text-align:right;list-style-type:none}.YearSelector li{margin-bottom:10px;margin-top:10px}

.MatchPage{display:grid;grid-template-columns:1fr 5fr;grid-column-gap:20px;grid-row-gap:10px}.MatchPage .year-selector{-webkit-justify-content:end;justify-content:end;padding:20px}.MatchPage .page-heading{margin:20px}

.TeamTitle{height:170px;text-align:center;border:1px solid;padding:20px}

.HomePage .team-grid{display:grid;grid-template-columns:1fr 1fr;grid-column-gap:10px;grid-row-gap:10px}.HomePage .app-name{font-size:3rem;margin:30px auto;text-align:center}

